@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500&family=Roboto:wght@300&family=Sofia+Sans+Condensed:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Ibarra+Real+Nova&family=Montserrat:wght@500&family=Roboto:wght@300&family=Sofia+Sans+Condensed:wght@700&family=Source+Serif+4:opsz,wght@8..60,500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Ibarra+Real+Nova&family=Montserrat:wght@500&family=Sofia+Sans+Condensed:wght@700&family=Source+Serif+4:opsz,wght@8..60,500&family=Spectral:ital,wght@0,200;1,200&display=swap");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 100%;
  overflow-x: hidden;
}

body {
  font-family: Helvetica, sans-serif;
  text-rendering: optimizeLegibility;
}

header {
  display: flex;
  padding: 20px 40px;
  background-color: #fff;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  width: 100%;
  overflow: hidden;
  top: 0;
}
header .logo {
  width: 3rem;
  margin-right: 1.8em;
}
header nav {
  align-items: center;
  color: #000;
  font-size: 0.7em;
  display: flex;
  flex: auto;
  text-decoration: none;
  justify-content: space-between;
  font-family: "Montserrat", sans-serif;
  letter-spacing: 2.2px;
  text-transform: uppercase;
  gap: 6.2rem;
}
header ul {
  display: flex;
}
header .nav-title {
  display: inline-block;
  text-decoration: none;
  color: #000;
  padding: 7.5px 15px;
  vertical-align: middle;
}
header .button {
  padding: 16px 32px;
  border-radius: 0;
  border: 1px solid #414141;
  margin-right: 1.4em;
}
header .header-icons {
  display: flex;
  align-items: center;
}
header .header-icons .list-icon {
  width: 28px;
  height: 28px;
  padding: 12px;
}

.hero {
  height: 100vh;
  width: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url("https://paint-box.com/cdn/shop/files/Web_Banner2.webp?v=1697146136&width=2400");
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #fff;
}
.hero h4 {
  font-family: "Roboto", sans-serif;
  font-size: 1em;
  letter-spacing: 2.2px;
  margin-bottom: 15px;
  text-transform: uppercase;
}
.hero h3 {
  font-size: 64px;
  font-family: "Spectral", serif;
  text-align: center;
  margin-bottom: 24px;
}
.hero p {
  font-family: "Spectral", serif;
  font-style: italic;
  font-size: 1.6em;
  margin-bottom: 5px;
  margin-top: 5px;
  vertical-align: middle;
}
.hero .cta-shop {
  background: #fff;
  border: none;
  color: #000;
  margin: 4px 3px 2px 2px;
  padding: 18px 40px;
  text-decoration: none;
  font-family: "Montserrat", sans-serif;
  font-size: 12px;
  letter-spacing: 2.8px;
  vertical-align: center;
  text-align: center;
  margin-top: 24px;
  text-transform: uppercase;
}

.index-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 75px 45px;
  margin: 0 6%;
  max-width: 4500px;
  column-gap: 2em;
  row-gap: 8em;
}
.index-wrapper .index-content {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #000;
}
.index-wrapper .index-content h4 {
  font-family: "Roboto", sans-serif;
  font-size: 1em;
  letter-spacing: 2.2px;
  margin-bottom: 15px;
  text-transform: uppercase;
}
.index-wrapper .index-content h3 {
  font-size: 30px;
  font-family: "spectral", serif;
  text-align: center;
  margin-bottom: 4px;
  line-height: 1.06em;
}
.index-wrapper .cta-shop-index {
  background: #000;
  border: none;
  color: #fff;
  margin: 4px 3px 2px 2px;
  padding: 18px 40px;
  text-decoration: none;
  font-family: "Montserrat", sans-serif;
  font-size: 12px;
  letter-spacing: 2.8px;
  vertical-align: center;
  text-align: center;
  margin-top: 24px;
  text-transform: uppercase;
}
.index-wrapper img {
  width: 100%;
  align-items: center;
  justify-content: center;
}

.shop-items-wrapper {
  padding: 75px 40px;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
}
.shop-items-wrapper h3 {
  font-size: 34px;
  font-family: "Spectral", serif;
  text-align: center;
  margin-bottom: 2.2em;
  color: #414141;
}
.shop-items-wrapper .shop-list {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  max-width: 1500px;
}
.shop-items-wrapper .shop-list .shop-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  object-fit: cover;
  overflow: hidden;
}
.shop-items-wrapper .shop-list .shop-item h2 {
  font-family: "freight-display-pro", "Times New Roman", serif;
  font-size: 30px;
  font-style: italic;
  background: #fff;
  line-height: 38px;
  margin: 20px 0px 10px 0;
  color: #414141;
}
.shop-items-wrapper .shop-list .shop-item h6 {
  color: #414141;
  margin: 5px;
  font-family: "Montserrat", sans-serif;
  font-size: 12px;
}
.shop-items-wrapper .shop-list .shop-item img {
  width: 100%;
  display: block;
}

.shop-banner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 40px;
  background-color: #f3ede8;
  padding: 130px 0;
  margin: 4em 0 9em 0;
}
.shop-banner h6 {
  font-family: "Roboto", sans-serif;
  font-size: 0.8em;
  letter-spacing: 2.2px;
  text-transform: uppercase;
  color: #414141;
}
.shop-banner h3 {
  font-size: 24px;
  font-family: "Spectral", serif;
  text-align: center;
  color: #414141;
  line-height: 1.4;
}

.collection-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 20px;
  padding: 0 20px;
}
.collection-wrapper .collection-content {
  height: 100vh;
  width: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url("/assets/collection-1.jpeg");
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #fff;
}
.collection-wrapper .collection-content h4 {
  font-family: "Roboto", sans-serif;
  font-size: 0.8em;
  letter-spacing: 2.2px;
  margin-bottom: 15px;
  text-transform: uppercase;
}
.collection-wrapper .collection-content h3 {
  font-size: 38px;
  font-family: "Spectral", serif;
  text-align: center;
  margin-bottom: 14px;
  line-height: 1.1;
}
.collection-wrapper .collection-content p {
  font-family: "Spectral", serif;
  font-style: italic;
  font-size: 1em;
  margin-bottom: 5px;
  margin-top: 5px;
  vertical-align: middle;
}
.collection-wrapper .collection-content .cta-shop {
  border: 2px solid #fff;
  color: #fff;
  margin: 4px 3px 2px 2px;
  padding: 18px 40px;
  text-decoration: none;
  font-family: "Montserrat", sans-serif;
  font-size: 12px;
  letter-spacing: 2.8px;
  vertical-align: center;
  text-align: center;
  margin-top: 24px;
  text-transform: uppercase;
}

.footer {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  text-align: center;
  font-family: "proxima-nova", "Helvetica", "Arial", sans-serif;
  font-size: 10.5px;
  letter-spacing: 2.2px;
  padding: 100px 65px;
  margin-top: 20px;
  text-transform: uppercase;
  background-color: #f3ede8;
}
.footer .footer-column {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.footer .footer-column .column-title {
  color: #9d9d9c;
  background-color: #f3ede8;
  font-family: proxima-nova, Helvetica, Arial, sans-serif;
  font-size: 10px;
  letter-spacing: 2.2px;
  margin-bottom: 20px;
  text-align: center;
  text-transform: uppercase;
  align-items: center;
  justify-content: center;
  vertical-align: middle;
}
.footer .footer-column ul {
  text-align: center;
}
.footer .footer-column ul li {
  color: #414141;
  margin-bottom: 15px;
}
.footer .footer-column:nth-child(2) {
  border-right: 1px solid #414141;
  border-left: 1px solid #414141;
}